<template>
<div>
  <!--    -->
  <el-breadcrumb separator="/">
    <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
  </el-breadcrumb>
  <el-card>
    <el-alert
        title="!"
        type="warning"
        show-icon>
    </el-alert>
<!--    -->
    <el-row>
      <el-col>
        <span>:</span>
<!--        -->
        <el-cascader
            expand-trigger="hover"
            v-model="selectedCateKeys"
            :options="cateList"
            :props="cateProps"
            @change="handleChange"
            clearable
            change-on-select
        >
        </el-cascader>
      </el-col>
    </el-row>
<!--    tab-->
    <el-tabs v-model="activeName" @tab-click="handleTabClick">
      <el-tab-pane label="" name="many">
        <el-button type="primary" size="mini" :disabled="isBtnDisabled" @click="addDialogVisible=true"></el-button>
<!--        -->
        <el-table :data="manyTableData" border stripe>
<!--          -->
          <el-table-column type="expand">
            <template slot-scope="scope">
<!--              tag-->
              <el-tag v-for="(item,index) in scope.row.attr_vals" :key="index" closable @close="handleClose(index,scope.row)">
                {{item}}
              </el-tag>
<!--              -->
              <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
              >
              </el-input>
<!--              -->
              <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
            </template>
          </el-table-column>
<!--          -->
          <el-table-column type="index"></el-table-column>
          <el-table-column label="" prop="attr_name"></el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row)"></el-button>
              <el-button type="danger" icon="el-icon-edit" size="mini" @click="removeParamById(scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>
      <el-tab-pane label="" name="only">
        <el-button type="primary" size="mini" :disabled="isBtnDisabled" @click="addDialogVisible=true"></el-button>
        <!--        -->
        <el-table :data="onlyTableData" border stripe>
          <!--          -->
          <el-table-column type="expand">
            <template slot-scope="scope">
              <!--              tag-->
              <el-tag v-for="(item,index) in scope.row.attr_vals" :key="index" closable @close="handleClose(index,scope.row)">
                {{item}}
              </el-tag>
              <!--              -->
              <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
              >
              </el-input>
              <!--              -->
              <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
            </template>
          </el-table-column>
          <!--          -->
          <el-table-column type="index"></el-table-column>
          <el-table-column label="" prop="attr_name"></el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row)"></el-button>
              <el-button type="danger" icon="el-icon-edit" size="mini" @click="removeParamById(scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>
    </el-tabs>
  </el-card>
  <el-dialog
      :title="''+titleText"
      :visible.sync="addDialogVisible"
      width="50%"
      @close="addDialogClosed"
  >
    <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px">
      <el-form-item :label="titleText" prop="attr_name">
        <el-input v-model="addForm.attr_name"></el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
    <el-button @click="addDialogVisible = false"> </el-button>
    <el-button type="primary" @click="addParams"> </el-button>
  </span>
  </el-dialog>
  <el-dialog
      :title="''+titleText"
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClosed"
  >
    <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="100px">
      <el-form-item :label="titleText" prop="attr_name">
        <el-input v-model="editForm.attr_name"></el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
    <el-button @click="editDialogVisible = false"> </el-button>
    <el-button type="primary" @click="editParamInfo"> </el-button>
  </span>
  </el-dialog>
</div>
</template>

<script>
export default {
  name: "Params",
  data(){
    return{
      cateList:[],
      //
      cateProps:{
        value:'cat_id',
        label:'cat_name',
        children:'children'
      },
      //
      selectedCateKeys:[],
      // 
      activeName:'many',
      manyTableData:[],
      onlyTableData:[],

      addDialogVisible:false,

      addForm:{
        attr_name:''
      },
      addFormRules:{
        attr_name:[
          {required: true, message: '', trigger: 'blur'},
          {min: 3, max: 10, message: ' 3  10 ', trigger: 'blur'}
        ]
      },
      editForm:{
        attr_name:''
      },
      editFormRules:{
        attr_name:[
          {required: true, message: '', trigger: 'blur'},
          {min: 3, max: 10, message: ' 3  10 ', trigger: 'blur'}
        ]
      },
      editDialogVisible:false,

    }
  },
  created() {
    this.getCateList()
  },
  methods:{
    async getCateList() {
      const {data:res}=await this.$http.get('categories')
      if (res.meta.status!==200){
        return this.$message.error('!')
      }
      this.cateList=res.data
    },
    async getParamsData(){
      if (this.selectedCateKeys.length!==3){
        this.selectedCateKeys=[]
        this.manyTableData=[]
        this.onlyTableData=[]
        return
      }
      console.log(this.selectedCateKeys)
      //  id
      const {data:res}=await this.$http.get(`categories/${this.cateId}/attributes`,{
        params:{
          sel:this.activeName
        }
      })
      if (res.meta.status!==200){
        return this.$message.error('')
      }
      res.data.forEach(item=>{
        item.attr_vals=item.attr_vals ? item.attr_vals.split(' ') : []
        // 
        item.inputVisible=false
        // 
        item.inputValue=''
      })
      console.log(res.data)
      if (this.activeName==='many'){
        this.manyTableData=res.data
      }else{
        this.onlyTableData=res.data
      }
    },
    //
    handleChange(){
      this.getParamsData()
    },
    // tab
    handleTabClick(){
      this.getParamsData()
    },
    addDialogClosed(){
      this.$refs.addFormRef.resetFields()
    },
    //or
    addParams(){
      this.$refs.addFormRef.validate(async valid=>{
        if (!valid)
          return
        const {data:res}=await this.$http.post(`categories/${this.cateId}/attributes`,{
          attr_name:this.addForm.attr_name,
          attr_sel:this.activeName,
        })
        if (res.meta.status!==201){
          return this.$message.error('')
        }
        this.$message.success('')
        this.addDialogVisible=false
        this.getParamsData()
      })
    },
    // or
    async showEditDialog(id){
      const {data:res}=await this.$http.get(`categories/${id.cat_id}/attributes/${id.attr_id}`,{
        attr_sel:this.activeName
      });
      this.editForm=res.data
      this.editDialogVisible=true
    },
    //
    editDialogClosed(){
      this.$refs.editFormRef.resetFields()
    },
    // or
    async editParamInfo(){
      this.$refs.editFormRef.validate(async valid=>{
        if (!valid)
          return
        const {data:res}=await this.$http.put(`categories/${this.editForm.cat_id}/attributes/${this.editForm.attr_id}`,{
          attr_name:this.editForm.attr_name,
          attr_sel:this.activeName
        })
        if (res.meta.status!==200){
          return this.$message.error('')
        }
        //
        this.editDialogVisible=false
        //
        this.getParamsData();
        //
        this.$message.success('')
      })
    },
    async removeParamById(id){
      const res=await this.$confirm(`${this.titleText}, ?`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err=>err)
      if (res!=='confirm'){
        return this.$message.info('')
      }
      const {data:response}=await this.$http.delete(`categories/${id.cat_id}/attributes/${id.attr_id}`)
      if (response.meta.status!==200){
        return this.$message.error('')
      }
      this.$message.success('')
      this.getParamsData();
    },
    //enter
    async handleInputConfirm(row){
      if (row.inputValue.trim().length===0){
        row.inputValue=''
        row.inputVisible=false
        return
      }
      row.attr_vals.push(row.inputValue.trim())
      row.inputValue=''
      row.inputVisible=false
      // 
      this.saveAttrValues(row)
    },
    //
    showInput(row){
      row.inputVisible=true
      //
      // $nextTick 
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },
    //
    handleClose(index,row){
      row.attr_vals.splice(index,1);
      this.saveAttrValues(row)
    },
    async saveAttrValues(row){
      const{data:res}=await this.$http.put(`categories/${this.cateId}/attributes/${row.attr_id}`,{
        attr_name:row.attr_name,
        attr_sel:row.attr_sel,
        attr_vals:row.attr_vals.join(' ')
      })
      if(res.meta.status!==200){
        return this.$message.error('')
      }
      this.$message.success('')
    }
  },
  computed:{
    isBtnDisabled(){
      if (this.selectedCateKeys.length!==3){
        return true
      }
      return false
    },
    cateId(){
      if (this.selectedCateKeys.length==3)
        return this.selectedCateKeys[2]
      return null
    },
    // 
    titleText(){
      if (this.activeName==='many'){
        return ''
      }
      return ''
    }
  }
}
</script>

<style lang="less" scoped>
  .el-row{
    margin: 15px 0;
  }
  .el-tag{
    margin: 0 10px;
  }
  .el-input{
    width: 120px;
  }
</style>